/**
 * Admin Page functionality.
 *
 * @since {{VERSION}}
 */

var ClientDash_AdminPage;

(function ($) {
    'use strict';

    var api = ClientDash_AdminPage = {

        /**
         * Initializes the api.
         *
         * @since {{VERSION}}
         */
        init: function () {

            if (!$('body').hasClass('client-dash_page_clientdash_admin_page')) {

                return;
            }

            $('#clientdash-admin-page-form').submit(api.handleFormSubmit);
        },

        /**
         * Fires on submitting the form for saving the admin page.
         *
         * @since {{VERSION}}
         */
        handleFormSubmit: function () {

            var $submit_button = $('#submit');

            $submit_button.prop('disabled', true );
        }
    }

    $(api.init);
})(jQuery);
/**
 * Settings Page functionality.
 *
 * @since {{VERSION}}
 */

var ClientDash_Settings;

(function ($) {
    'use strict';

    var l10n = typeof ClientDash_Data != 'undefined' ? ClientDash_Data.l10n : {};

    var api = ClientDash_Settings = {

        /**
         * Initializes the api.
         *
         * @since {{VERSION}}
         */
        init: function () {

            if (!$('body').hasClass('client-dash_page_clientdash_settings')) {

                return;
            }

            $('#cd_reset_all_settings').click(api.confirmReset);
        },

        /**
         * Confirms resetting CD settings.
         *
         * @since {{VERSION}}
         */
        confirmReset: function (e) {

            if ( !confirm(l10n['reset_settings_confirm'])) {

                e.preventDefault();
            }
        }
    }

    $(api.init);
})(jQuery);
/**
 * Handles functionality for the Dashicons Selector field.
 *
 * @since {{VERSION}}
 */
(function ($, data) {
    'use strict';

    var l10n = data.l10n;

    var api = {

        $fields: [],

        init: function () {

            api.$fields = $('.cd-dashicon-selector');

            if (!api.$fields.length) {

                return;
            }

            $('body, html').click(api.closeSelectors);
            api.$fields.find('li').click(api.selectIcon);
            api.$fields.find('[data-toggle]').click(api.toggle);
        },

        selectIcon: function (e) {

            e.stopPropagation();

            var icon = $(this).attr('data-icon');

            $(this).siblings('li').removeClass('cd-dashicon-selector-selected');
            $(this).addClass('cd-dashicon-selector-selected');
            $(this).closest('.cd-dashicon-selector').find('> input[type="hidden"]').val(icon);
            $(this).closest('.cd-dashicon-selector').find('.cd-dashicon-selector-preview').attr('class',
                'cd-dashicon-selector-preview dashicons ' + icon
            );
            $(this).closest('.cd-dashicon-selector').removeClass('cd-dashicon-selector-open');
        },

        toggle: function (e) {

            console.log('toggle');
            e.stopPropagation();

            $(this).closest('.cd-dashicon-selector').toggleClass('cd-dashicon-selector-open');
        },

        closeSelectors: function () {

            api.$fields.removeClass('cd-dashicon-selector-open');
        }
    }

    $(api.init);
})(jQuery, ClientDash_Data);